.settings-modal-light {
  margin: 10px 10px;
  padding: 10px 10px;
  box-shadow: var(--box-shadow-light);
  height: 100vh;
}

.settings-modal-dark {
  margin: 10px 10px;
  padding: 10px 10px;
  box-shadow: var(--box-shadow-dark);
  height: 100vh;
}

h1 {
  text-align: center;
}

.color-theme {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}


.theme-switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.theme-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgb(255, 224, 224);
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: rgb(77, 77, 77);
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.difficulty-select {
  display: flex;
  flex-direction: column;
}

h3 {
  margin-top: 2rem;
}

.difficulty-select div {
  margin-bottom: 10px;
}

.difficulty-select label {
  padding-left: 10px;

}