@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,400;0,700;1,200&display=swap);
:root {
  --bg-color-light: #ffbead;
  --box-shadow-light: 6px 6px 12px #d9a293, 
                      -6px -6px 12px #ffdbc7;
  --box-shadow-light-inverse: inset 6px 6px 12px #d9a293, 
                              inset -6px -6px 12px #ffdbc7;
  --font-color-light: black;

  --bg-color-dark: #3d0035;
  --box-shadow-dark: 8px 8px 16px #34002d, 
                      -8px -8px 16px #46003d;
  --box-shadow-dark-inverse: inset 8px 8px 16px #34002d, 
                              inset -8px -8px 16px #46003d;
  --font-color-dark: white;

  --font-stack: 'Josefin Sans', sans-serif;
}

body {
  margin: 0;
  width: 100%;
  height: 100%;
}



nav {
  width: 100%;
  padding: 10px 20px;
  margin-bottom: 5vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-top: 1vh
}

nav h1 {
  text-transform: uppercase;
  font-size: 2rem;
}

nav div {
  margin-right: 10%;
  margin-left: auto;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

footer {
  height: 10vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: rgb(255, 129, 107);
  font-weight: 700;
}

#settings-icon-light {
  box-shadow: var(--box-shadow-light);
  padding: .5rem;
  border-radius: 50%;
  text-transform: uppercase;
  font-size: 2rem;
  background: none;
  border: none;
}

#settings-icon-dark {
  box-shadow: var(--box-shadow-dark);
  padding: .5rem;
  border-radius: 50%;
  text-transform: uppercase;
  font-size: 2rem;
  background: none;
  border: none;
}

#settings-icon-light:hover {
  box-shadow: var(--box-shadow-light-inverse);
}

#settings-icon-dark:hover {
  box-shadow: var(--box-shadow-dark-inverse);
}

.light-mode {
  margin: 0;
  padding: 0;
  font-family: var(--font-stack);
  background-color: var(--bg-color-light);
  color: var(--font-color-light);
  width: 100%;
}

.dark-mode {
    margin: 0;
    padding: 0;
    font-family: var(--font-stack);
    background-color: var(--bg-color-dark);
    color: var(--font-color-dark);
    width: 100%;
}

#button-light {
  background: none;
  border: solid 1px rgba(122, 122, 122, 0.5);
  padding: 1.5rem;
  font-family: var(--font-stack);
  font-size: 1.2rem;
  margin: 1rem 0;
  border-radius: 5px;
  color: var(--font-color-light)
}

#button-light:hover {
  border: solid 1px rgba(255, 255, 255, 1);
  
} 

#button-dark {
  background: none;
  border: solid 1px rgba(122, 122, 122, 0.5);
  padding: 1.5rem;
  font-family: var(--font-stack);
  font-size: 1.2rem;
  margin: 1rem 0;
  border-radius: 5px;
  color: var(--font-color-dark)
}

#button-dark:hover {
  border: solid 1px rgba(255, 255, 255, 1);
} 

.success {
  background-color: rgba(69, 240, 39, 0.411);
  padding: 2rem;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
}

.failure {
  color: black;
  background-color: rgba(255, 53, 53, 0.411);
  padding: 2rem;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
}

.scoreboard-light {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: -2rem;
  margin-bottom: 2rem;
  box-shadow: var(--box-shadow-light-inverse);
  padding: 0.2rem 2rem;
}

.scoreboard-dark {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: -2rem;
  margin-bottom: 2rem;
  box-shadow: var(--box-shadow-dark-inverse);
  padding: 0.2rem 2rem;
}

.scoreboard-light div {
  margin: 0px 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
 
.scoreboard-light div h4 {
  margin-bottom: 0px;
}


.scoreboard-dark div {
  margin: 0px 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.scoreboard-dark div h4 {
  margin-bottom: 0px;
}


.counter-frame-light {
  box-shadow: var(--box-shadow-light);
  border-radius: 50%;
  margin-bottom: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.counter-frame-dark {
  box-shadow: var(--box-shadow-dark);
  border-radius: 50%;
  margin-bottom: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.counter-frame-inset-light {
  margin: 20px;
  border-radius: 50%;
  box-shadow: var(--box-shadow-light-inverse);
}

.counter-frame-inset-dark {
  margin: 20px;
  border-radius: 50%;
  box-shadow: var(--box-shadow-dark-inverse);
}

.counter-frame-inset img {
  padding: 10px;
  height: 150px;
  width: 150px;
}
.word-box-light {
  max-width: 700px;
  padding: 5px 50px;
  margin-bottom: 2vh;
  box-shadow: var(--box-shadow-light);
  border-radius: 10px;
}

.word-box-dark {
  max-width: 700px;
  padding: 5px 50px;
  margin-bottom: 2vh;
  box-shadow: var(--box-shadow-dark);
  border-radius: 10px;
}
.letter-box-light {
  box-shadow: var(--box-shadow-light);
  background-color: var(--bg-color-light);
  border: none;
  height: 3rem;
  width: 3rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 10px 10px;
}

.letter-box-light:focus {
  border: none;
  outline: none;
}

.letter-box-light:hover {
  border: solid 1px white;
}

.letter-box-dark {
  box-shadow: var(--box-shadow-dark);
  background-color: var(--bg-color-dark);
  color: white;
  border: none;
  height: 3rem;
  width: 3rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 10px 10px;
}

.letter-box-dark:focus {
  border: none;
  outline: none;
}

.letter-box-dark:hover {
  border: solid 1px white;
}

.letter-box-clicked-light {
  box-shadow: var(--box-shadow-light-inverse);
  height: 3rem;
  width: 3rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 10px 10px;
  opacity: 0.3;
}

.letter-box-clicked-dark {
  box-shadow: var(--box-shadow-dark-inverse);
  height: 3rem;
  width: 3rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 10px 10px;
  opacity: 0.3;
}
.alphabet-selection {
  display: flex;
  flex-direction: column;
}

.consonant-rows {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 700px;
}

.vowel-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
#definition:before {
  content: "Definition: ";
  font-weight: 700;
}

#definition-block {
  max-width: 700px;
  margin-bottom: 3vh;
  margin-top: 3vh;
  padding: 10px 20px;
  /* display: none; */
}

#example {
  font-style: italic;
  font-weight: 100;
}

#part-of-speech {
  margin-bottom: 0;
  padding-bottom: -5px;
  margin-top: 3rem;
}
.settings-modal-light {
  margin: 10px 10px;
  padding: 10px 10px;
  box-shadow: var(--box-shadow-light);
  height: 100vh;
}

.settings-modal-dark {
  margin: 10px 10px;
  padding: 10px 10px;
  box-shadow: var(--box-shadow-dark);
  height: 100vh;
}

h1 {
  text-align: center;
}

.color-theme {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}


.theme-switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.theme-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgb(255, 224, 224);
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
}

input:checked + .slider {
  background-color: rgb(77, 77, 77);
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.difficulty-select {
  display: flex;
  flex-direction: column;
}

h3 {
  margin-top: 2rem;
}

.difficulty-select div {
  margin-bottom: 10px;
}

.difficulty-select label {
  padding-left: 10px;

}
